
import { ref, type Ref, onMounted, watch, onUnmounted } from "vue";
import { useStore } from "vuex";
import { key } from "@/store";

import { useRoute, useRouter } from "vue-router";

export default {
  props: {
    diashowName: String,
  },
  setup(props) {
    const store = useStore(key);
    let route = useRoute();
    const router = useRouter();

    const diashowDirection: Ref<"left" | "right" | "initial"> = ref("initial");

    //Diashows
    const diashowLists = {
      dance: [
        "/images/diashow/dance/dance-01.jpg",
        "/images/diashow/dance/dance-02.jpg",
        "/images/diashow/dance/dance-03.jpg",
      ],
    };

    if (props.diashowName === undefined) {
      console.warn("no diashow name found");
    }

    //Current Diashow
    let currentDiashowList: Ref<string[]> = ref([]);

    params(route.params);

    watch(
      () => route.params,
      (current, previous) => {
        params(current);
      },
      {
        deep: true,
      }
    );

    function params(params: any) {
      try {
        const selectedDiashow = diashowLists[props.diashowName ?? "dance"] as [];
        currentDiashowList.value = selectedDiashow;
        store.state.mainView.currentGalleryLength = currentDiashowList.value.length;
      } catch (error) {
        console.warn(`No diashow found for: '${props.diashowName}'`);
      }

      if (params.galleryIndex) {
        if (isNaN(Number(params.galleryIndex))) {
          store.state.mainView.currentGalleryIndex = 0;
        } else {
          store.state.mainView.currentGalleryIndex = Number(params.galleryIndex);
        }

        if (store.state.mainView.currentGalleryIndex < 0) {
          store.state.mainView.currentGalleryIndex = 0;
          console.log("smaller than zero");
          updateIndexInUrl();
        }

        if (store.state.mainView.currentGalleryIndex > store.state.mainView.currentGalleryLength) {
          store.state.mainView.currentGalleryIndex = store.state.mainView.currentGalleryLength;
          updateIndexInUrl();
        }
      }
    }

    function handleScroll(event) {
      const delta = Math.sign(event.deltaY);
      if (delta > 0) {
        //
        // console.info("right");
        nextImage();
      }
      if (delta < 0) {
        // console.info("left");
        previousImage();
      }
    }

    // left: 37, up: 38, right: 39, down: 40,
    // spacebar: 32, pageup: 33, pagedown: 34, end: 35, home: 36
    var keys = { 37: 1, 38: 1, 39: 1, 40: 1 };

    function preventDefault(e) {
      e.preventDefault();
    }

    function preventDefaultForScrollKeys(e) {
      if (keys[e.keyCode]) {
        preventDefault(e);
        return false;
      }
    }

    // modern Chrome requires { passive: false } when adding event
    var supportsPassive = false;
    try {
      window.addEventListener(
        "auxclick",
        () => {},
        Object.defineProperty({}, "passive", {
          get: function () {
            supportsPassive = true;
          },
        })
      );
    } catch (e) {}

    var wheelOpt = supportsPassive ? { passive: false } : false;
    var wheelEvent = "onwheel" in document.createElement("div") ? "wheel" : "mousewheel";

    // call this to Disable
    function disableScroll() {
      const mainImage = document.getElementById("main-image");
      window.addEventListener("DOMMouseScroll", preventDefault, false); // older FF
      window.addEventListener(wheelEvent, preventDefault, wheelOpt); // modern desktop
      window.addEventListener("touchmove", preventDefault, wheelOpt); // mobile
      window.addEventListener("keydown", preventDefaultForScrollKeys, false);
    }

    // call this to Enable
    function enableScroll() {
      window.removeEventListener("DOMMouseScroll", preventDefault, false);
      window.removeEventListener(wheelEvent, preventDefault, false);
      window.removeEventListener("touchmove", preventDefault, false);
      window.removeEventListener("keydown", preventDefaultForScrollKeys, false);
    }

    onMounted(() => {
      window.addEventListener("wheel", handleScroll);
      disableScroll();
    });

    onUnmounted(() => {
      window.removeEventListener("wheel", handleScroll);
      enableScroll();
    });

    function updateIndexInUrl() {
      //set url
      router.push({
        name: "home_gallery_index",
        params: {
          gallery: store.state.mainView.currentGalleryId,
          galleryIndex: store.state.mainView.currentGalleryIndex,
        },
      });
    }

    function clickOutside() {
      console.log("clicked");
      store.dispatch("hideGallery", false);
      //set url
      router.push({
        name: "home",
      });
    }

    function goToImage(parameter: { index: number; currentIndex: number }) {
      console.log("clicked index");
      console.log(parameter);
      store.dispatch("gotoGalleryIndex", parameter.index);
      updateIndexInUrl();

      if (parameter.currentIndex < parameter.index) {
        diashowDirection.value = "right";
      }
      if (parameter.currentIndex > parameter.index) {
        diashowDirection.value = "left";
      }
    }

    function nextImage() {
      console.log("clicked mext");
      store.dispatch("nextGalleryIndex", false);
      updateIndexInUrl();
      diashowDirection.value = "right";
    }

    function previousImage() {
      console.log("clicked prev");
      store.dispatch("previousGalleryIndex", false);
      updateIndexInUrl();
      diashowDirection.value = "left";
    }
    let grabStartingPoint: number;
    function onGrabImage(event) {}
    function onGrabEndImage(event) {}
    function onGrabMoveImage(event) {}
    function onScrollImage(event) {}

    return {
      diashow: {
        currentDiashowList,
        diashowDirection,
        // currentDiashowIndex,
      },
      store,
      functions: {
        previousImage,
        nextImage,
        clickOutside,
        goToImage,

        onGrabImage,
        onGrabEndImage,
        onGrabMoveImage,
        onScrollImage,
      },
    };
  },
};
