
import { ref, onMounted, Ref, onUpdated } from 'vue';
import diashow from '@/components/diashow.vue';
import circleLoader from '@/components/circleLoader.vue';

// import { useStore } from 'vuex';
// import { key } from '@/store';
import { store } from '@/store';

import { useRoute, useRouter } from 'vue-router';

import { clickOnLink } from '@/functions/link-without-redirect';

export default {
  components: {
    diashow,
    circleLoader,
  },
  setup() {
    // const store = theStore;
    const route = useRoute();
    const router = useRouter();

    const routeComponent = ref<HTMLElement | null>(null);

    let homeShowRegularLogo = ref(false);
    let timerId: null | number = null;

    function hanldeScroll() {
      const scrollElements: NodeListOf<Element> = document.querySelectorAll('.route-component')!;
      let scrollElement = scrollElements[0];
      if (scrollElements.length > 1) {
        scrollElement = scrollElements[scrollElements.length - 1];
      }
      console.log('a');
      if (!scrollElement) {
        return;
      }
      console.log(scrollElement);

      // scrollElement.onchange = (c) => {
      //   console.log('c');
      //   console.log(c);
      // };

      scrollElement.addEventListener('scroll', function () {
        if (scrollElement.scrollTop == 0) {
          //set navbar transparent
          navbarTransparent.value = true;
          homeShowRegularLogo.value = false;
          if (timerId !== null) {
            clearTimeout(timerId);
          }
        } else {
          navbarTransparent.value = false;

          if (timerId === null) {
            timerId = setTimeout(() => {
              homeShowRegularLogo.value = true;
            }, 0.5);
          }
        }

        var currentScrollPos = scrollElement.scrollTop;
        if (prevScrollpos > currentScrollPos) {
          navbarShown.value = true;
        } else {
          if (currentScrollPos > window.innerHeight / 2) {
            navbarShown.value = false;
          }
        }
        prevScrollpos = currentScrollPos;
      });
    }

    const isHomePage = ref(false);
    router.afterEach((to, from) => {
      // to and from are both route objects.
      if (to.name == 'home') {
        isHomePage.value = true;
      } else {
        isHomePage.value = false;
      }
    });

    function routeEnter() {
      console.log('enter');
      hanldeScroll();
    }

    let navbarState: Ref<'closed' | 'open'> = ref('closed');

    const navbarTransparent = ref(true);
    const navbarShown = ref(true);

    const showModal = ref(false);

    const loaderTimings = {
      instantLoadingTimer: 0.1,
      minDisplatTime: 1,
      maxDisplayTime: 10,
      currentDisplayTime: 0,
      isLoaded: false,
      showLoader: ref(true),
    };
    const loadingTimer = setInterval(() => {
      loaderTimings.currentDisplayTime += 0.1;
      if (
        loaderTimings.isLoaded &&
        loaderTimings.minDisplatTime <= loaderTimings.currentDisplayTime
      ) {
        loaderTimings.showLoader.value = false;
        clearInterval(loadingTimer);
      }
    }, 100);

    //hide on scroll
    let prevScrollpos = window.pageYOffset;
    onMounted(() => {
      window.addEventListener('load', function (event) {
        loaderTimings.isLoaded = true;
      });
    });

    function closeNavbar() {
      navbarState.value = 'closed';
    }

    function toggleNavbar() {
      if (navbarState.value === 'closed') {
        navbarState.value = 'open';
      } else {
        navbarState.value = 'closed';
      }
    }

    function gotoHome(event) {
      event.preventDefault();

      router.push({
        name: 'home',
      });
    }
    return {
      isHomePage,
      routeEnter,
      routeComponent,
      store,
      navbarState,
      showLoader: loaderTimings.showLoader,
      showModal,
      navBar: {
        prevScrollpos,
        navbarShown,
        navbarTransparent,
      },
      functions: {
        toggleNavbar,
        closeNavbar,
        clickOnLink,
      },
    };
  },
};
