export class ShowOnScroll {
  private elements: NodeListOf<Element>;
  constructor(parameter: {
    selector: string;
    visibleClassName: string;
    hideAgainAfterScroll?: boolean;
    applyOnChildElemetSelector?: string;
  }) {
    this.elements = document.querySelectorAll(parameter.selector);

    const observer = new IntersectionObserver(
      (entries) => {
        for (const entry of entries) {
          if (entry.isIntersecting) {
            //in viewport
            // eslint-disable-next-line unicorn/no-negated-condition
            if (parameter.applyOnChildElemetSelector !== undefined) {
              const childrenOnTarget = entry.target.querySelectorAll(
                parameter.applyOnChildElemetSelector
              );
              // eslint-disable-next-line unicorn/no-array-for-each
              childrenOnTarget.forEach((childTarget) => {
                childTarget.classList.add(parameter.visibleClassName);
              });
            } else {
              entry.target.classList.add(parameter.visibleClassName);
            }
          }

          if (parameter.hideAgainAfterScroll === true && !entry.isIntersecting) {
            // eslint-disable-next-line unicorn/no-negated-condition
            if (parameter.applyOnChildElemetSelector !== undefined) {
              const childrenOnTarget = entry.target.querySelectorAll(
                parameter.applyOnChildElemetSelector
              );
              // eslint-disable-next-line unicorn/no-array-for-each
              childrenOnTarget.forEach((childTarget) => {
                childTarget.classList.add(parameter.visibleClassName);
                childTarget.classList.remove(parameter.visibleClassName);
              });
            } else {
              entry.target.classList.remove(parameter.visibleClassName);
            }
          }
        }
      },
      { threshold: 0.5 }
    );

    // eslint-disable-next-line unicorn/no-array-for-each
    this.elements.forEach((element) => observer.observe(element));
  }
}
