import { createRouter, RouteRecordRaw, createWebHistory } from 'vue-router';

import HomeView from '../views/home-view.vue';
import aboutView from '../views/about-view.vue';
import printsView from '../views/prints-view.vue';
import workshopsView from '../views/workshops-view.vue';
import studioView from '../views/studio-view.vue';
import newsletterView from '../views/newsletter-view.vue';

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    children: [
      {
        name: 'home_gallery',
        path: 'gallery/:gallery',
        component: HomeView,
        children: [
          { name: 'home_gallery_index', path: 'index/:galleryIndex', component: HomeView },
        ],
      },
    ],
  },
  {
    path: '/about',
    name: 'about',
    component: aboutView,
  },
  {
    path: '/prints',
    name: 'prints',
    component: printsView,
  },
  {
    path: '/workshops',
    name: 'workshops',
    component: workshopsView,
  },
  {
    path: '/studio',
    name: 'studio',
    component: studioView,
  },
  {
    path: '/newsletter',
    name: 'newsletter',
    component: newsletterView,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
