
//? Main page images paralag zoom effect?
import { onMounted, ref } from 'vue';
// import { useStore } from 'vuex';
import { store } from '@/store';
import { useRoute, useRouter } from 'vue-router';
import footerComponent from '@/components/footer.vue';

// import footerComponent from "@/components/footer.vue";

import { ShowOnScroll } from '@/functions/show-on-scroll';

export default {
  components: {
    footerComponent,
  },
  setup() {
    // const store = useStore(key);
    const route = useRoute();
    const router = useRouter();

    function showGallery(galleryName: string, event) {
      // console.log(event);
      event.preventDefault();
      //set url
      router.push({
        name: 'home_gallery_index',
        params: { gallery: galleryName, galleryIndex: 1 },
      });
      store.state.mainView.currentGalleryId = galleryName;
    }

    onMounted(() => {
      new ShowOnScroll({
        selector: '#third-section a',
        visibleClassName: 'show-on-scroll-visible',
        // applyOnChildElemetSelector: "a",
      });
    });

    return {
      functions: {
        showGallery,
      },
    };
  },
};
