/* eslint-disable @typescript-eslint/no-unused-vars */
import { InjectionKey } from 'vue';
import { Store, createStore, useStore } from 'vuex';

import { dictionary } from './dictionary';

export interface State {
  mainView: {
    currentGalleryId: string | undefined;
    currentGalleryLength: number;
    currentGalleryIndex: number;
  };
  currentLanguage: 'de' | 'en';
}

export const key: InjectionKey<Store<State>> = Symbol();

// export const theStore = useStore(key);

export const store = createStore<State>({
  state: {
    mainView: {
      currentGalleryId: undefined,
      currentGalleryLength: 0,
      currentGalleryIndex: 0,
    },
    currentLanguage: 'de',
  },
  getters: {
    getLanguageWord:
      (state, getters) =>
      // eslint-disable-next-line unicorn/consistent-function-scoping
      (word): string => {
        try {
          if (word in dictionary === undefined) {
            console.warn(`No Translation found for: ${word}`);
            return '';
          }

          if (!(state.currentLanguage in dictionary[word])) {
            console.warn(`No Language found in Translation for: ${word}`);
            return '';
          }
          return dictionary[word][state.currentLanguage];
        } catch (error) {
          console.warn(error);
          return '';
        }
      },
  },
  mutations: {},
  actions: {
    hideGallery(context) {
      this.state.mainView.currentGalleryId = undefined;
      this.state.mainView.currentGalleryIndex = 1;
      this.state.mainView.currentGalleryLength = 0;
    },
    gotoGalleryIndex(context, newIndex) {
      // this.state.mainView.currentGalleryId = undefined;
      if (this.state.mainView.currentGalleryId === undefined) {
        return;
      }
      if (this.state.mainView.currentGalleryLength === 0) {
        this.state.mainView.currentGalleryIndex = 1;
        return;
      }

      if (this.state.mainView.currentGalleryIndex > this.state.mainView.currentGalleryLength) {
        //is larger than max length
        this.state.mainView.currentGalleryIndex = this.state.mainView.currentGalleryLength;
        return;
      }
      if (this.state.mainView.currentGalleryIndex < 0) {
        //is smaller than max length
        this.state.mainView.currentGalleryIndex = 0;
        return;
      }

      // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      this.state.mainView.currentGalleryIndex = newIndex;
    },
    nextGalleryIndex(context) {
      // this.state.mainView.currentGalleryId = undefined;
      if (context.state.mainView.currentGalleryId === undefined) {
        return;
      }
      if (this.state.mainView.currentGalleryLength === 0) {
        this.state.mainView.currentGalleryIndex = 1;
        return;
      }
      if (
        context.state.mainView.currentGalleryIndex === context.state.mainView.currentGalleryLength
      ) {
        return;
      }
      if (
        context.state.mainView.currentGalleryIndex > context.state.mainView.currentGalleryLength
      ) {
        //is larger than max length
        this.state.mainView.currentGalleryIndex = this.state.mainView.currentGalleryLength;
        return;
      }
      if (context.state.mainView.currentGalleryIndex < 0) {
        //is smaller than max length
        this.state.mainView.currentGalleryIndex = 0;
        return;
      }

      // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      this.state.mainView.currentGalleryIndex!++;
    },
    previousGalleryIndex(context) {
      // this.state.mainView.currentGalleryId = undefined;

      if (this.state.mainView.currentGalleryId === undefined) {
        return;
      }
      if (this.state.mainView.currentGalleryLength === 0) {
        this.state.mainView.currentGalleryIndex = 1;
        return;
      }
      console.log('pre');
      // if (this.state.mainView.currentGalleryIndex === this.state.mainView.currentGalleryLength) {
      //   return;
      // }
      if (this.state.mainView.currentGalleryIndex > this.state.mainView.currentGalleryLength) {
        //is larger than max length
        this.state.mainView.currentGalleryIndex = this.state.mainView.currentGalleryLength;
        return;
      }
      if (this.state.mainView.currentGalleryIndex <= 1) {
        //is smaller than max length
        this.state.mainView.currentGalleryIndex = 1;
        return;
      }

      // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
      this.state.mainView.currentGalleryIndex!--;
    },
  },
  modules: {},
});
